import { createStore } from 'vuex';
import router from '../router';
import {showNotification} from "../components/functions/js-notification";


const Storage = window.localStorage;

export default createStore({
  state: {
    isFetching: false,
    users: [
      {username: "Julia"},
      {username: "Julia2"},
      {username: "Julia3"},
      {username: "Julia4"},
      {username: "Julia5"},
      {username: "Julia6"},
    ],
    searchChars: "",
    searchedUsers: [],
    currentUser: {},
    isUserLoggedin: false,
    receiverId: null,
  },
  mutations: {
    UPDATE_IS_FETCHING(state, value){
      state.isFetching = value;
    },
    UPDATE_USERS(state, users){
      state.users = users;
    },
    UPDATE_SEARCHED_USERS(state, value){
      state.searchChars = value;
    },
    SET_USER(state, user){
      // state.currentUser.token = user.token;
      // state.currentUser.username = user.username;
      // state.currentUser.id = user.id;
      Storage.token = user.token;
      Storage.username = user.username;
      Storage.id = user.id;
      Storage.isLoggedIn = true;
    },
    SET_RECEIVER_ID(state, id){
      // state.receiverId = id;
      Storage.receiverId = id;
    }
  },
  actions: {
    async FETCH_USERS(ctx){
      try{
        const res = await fetch(`https://web-notifications.ru/users`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${window.localStorage.token}`
          },
          credentials: 'include',
          mode: 'cors',
        });
        const data =  await res.json();
        ctx.commit('UPDATE_USERS', data.users);

      }
      catch (err) {
        console.log(err);
      }
    },
    async FETCH_LOGIN(ctx, user){
      try{
        const username = user.username;
        const password = user.password;
        const url = 'https://web-notifications.ru/login';
        const res = await fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        mode: 'cors',
        body: JSON.stringify({username, password})
    });
    const response = await res.json();
    if (response.status == 200) {
        Storage.userId = response.user.id;
        Storage.login = response.user.username;
        Storage.token = response.user.token;
        ctx.commit("SET_USER", response.user);
        router.push({name: "Main"});
    } else {
        console.log('something wrong')
    }
      }
      catch(err){
        console.log(err);
      }
    },
    async SEND_MESSAGE(ctx, msg){
      const title = msg.title;
      const body = msg.body;
      const id = Storage.receiverId;
      const payload = {id, title, body}
      const res = await fetch('https://web-notifications.ru/sent-notifications', {
          method: 'POST',
          credentials: 'include',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${window.localStorage.token}`
          },
          body: JSON.stringify(payload)
      });
      const data = await res.json();
      console.log(data);
    }
  },
  getters: {
    GET_IS_FETCHING(state){
      return state.isFetching;
    },
    GET_USERS(state){
      return state.users;
    },
    GET_SEARCHED_USERS(state){
      if (!state.searchChars.length) return state.users;
      state.searchedUsers = [];
      for (let i = 0; i < state.users.length; i++) {
        const user = state.users[i];
        if (user.name.toLowerCase().includes(state.searchChars.toLowerCase())) {
          state.searchedUsers.push(user);
        }
      }
      return state.searchedUsers;
    },
    GET_IS_LOGGED_IN(state){
      // return state.isUserLoggedin;
      return Storage.isLoggedIn;
    },
    GET_RECEIVER_ID(state){
      // return state.receiverId;
      return Storage.receiverId;
    }
  }
});
